import React from 'react';
import kebabCase from 'lodash/kebabCase';
import { Link } from 'gatsby';
import styles from './Tag.module.scss';

type Props = {
  text: string;
  totalCount: number;
  fieldValue: string;
};

const Tag = ({ text, totalCount, fieldValue }: Props) => (
  <Link to={`/tag/${kebabCase(fieldValue)}/`} className={styles['tag']}>
    {text} ({totalCount})
  </Link>);

export default Tag;